        flex_shrink: 1,
        flex_basis: 0,
        align_self: 'auto'
      })
    }
    this.hljs.highlightBlock(this.$refs['flex-grow-code-html'])
    this.hljs.highlightBlock(this.$refs['flex-grow-code-css'])
    this.hljs.highlightBlock(this.$refs['flex-shrink-code-html'])
    this.hljs.highlightBlock(this.$refs['flex-shrink-code-css'])
  },
  methods: {
    formatTen (val) {
      return val / 10
    },
    formatOrder (val) {
      let res = (val - 50) / 10
      return res.toString()
    },
    formatHeight (val) {
      if (val === 0) {
        return 'auto'
      }
      return val + 'px'
    },
    formaParrenttHeight (val) {
      if (val === 0) {
        return 'auto'
      }
      return val * 5 + 'px'
    },
    formatFlexBasis (val) {
      if (val === 0) {
        return 'auto'
      }
      return val * 10 + 'px'
    }
  },
  watch: {
    child_num: function (val, oldval) {
      if (val === oldval) return
      if (val > oldval) {
        for (let i = 0; i < (val - oldval) / 10; i++) {
          this.child.push({
            key: new Date().getTime() + Math.random(),
            height: 50,
            order: 50,
            flex_grow: 0,
            flex_shrink: 1,
            flex_basis: 0,
            align_self: 'auto'
          })
        }
      } else {
        for (let i = 0; i < (oldval - val) / 10; i++) {
          this.child.pop()
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main-content {
