}
.child-conf {
  float: left;
  padding: 10px;
  width: 220px;
  border: 1px solid #e9e9e9;
  border-radius: 4px;
  margin-right: 5px;
  margin-bottom: 5px;
  transition: all ease 1s;
}
.child-conf .label {
  height: 36px;
  line-height:36px;
  color:#48576a;
}
.des .item {
  padding: 30px;
  border: 1px solid #e9e9e9;
  border-radius: 4px;
}
.des .cankao {
  margin-bottom: 20px;
}
.des .item:not(:last-of-type) {
  margin-bottom: 30px;
}
.des .item>div>span {
  padding: 10px 0;
  color:#48576a;
}
.des .item>div>span.label {
  padding-right: 10px;
  font-weight: bold;
}
.des .label.sub {
  padding-left: 20px;
}
.table {
  margin-top: 25px;
}
img.image {
  width: 100%;
}
.img-des {
  padding: 14px;
  line-height: 35px;
}
.flex-grow-code {
  width: 80%;
  margin: 20px auto;
}
.flex-grow-code>.note {
  margin: 10px 0 0;
  line-height: 35px;
}
.flex-grow-code>pre {
  display: flex;
  align-items: stretch;
}
.flex-grow-code>pre>code {
  width: 65%;
  margin-right: 2%;
}
.flex-grow-code>pre>code:first-of-type {
  width: 30%;
}
</style>



